import Vue from 'vue';
import VueRouter from 'vue-router';
import labelManagementRouter from '@/router/labelManagement';
import learningPlatformRouter from '@/router/learningPlatform';
import AuthRouter from '@/router/auth';
import ExceptionRouter from '@/router/exception';

const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location)
    .catch((err) => err);
};

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    component: () => import('@/layout/index.vue'),
    redirect: '/learningPlatform',
    children: [
      learningPlatformRouter,
      labelManagementRouter,
    ],
  },
  AuthRouter,
  ExceptionRouter,
  {
    path: '*', redirect: '/404',
  },
];

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
